import React from 'react'
import TplGlobal from './template-global'
import BlockInfo from '../blocks/blockInfo'
import BlockProject from '../blocks/blockProject'
import Interests from '../blocks/interests'
import Input from '../form/input'
import Button from '../form/button'
import {mockProjects, } from '../mocks/mocks'
import * as users from '../mocks/data/users'
import BlockUsers from '../blocks/users'
import BlockInterests from '../blocks/interests'
import ProjectTypeIconContainer from '../icons/containers/projectType'

const TplList = (props) => {
    const {title, children} = props;

    const eposters = [
        {
            type: 1,
            text: "Acetaminophen/Paracetamol for pain during pregnancy: Good or bad?"
        },
    ]

    return (
    

        <TplGlobal title={title}>
            <div className="main">
               <div className="sections">

                    <div className="section list">
                        
                        {children}

                    </div>
                    <div className="section blocks">
                        
                        <BlockInfo title="Top ePosters">
                            {
                                eposters.map((item, idx)=> {
                                    return (
                                        
                                        <div className="ePoster">
                                            <ProjectTypeIconContainer type={item.type} /> {item.text}

                                        </div>
                                    )
                                })
                            }
                        </BlockInfo>
                        
                        <BlockInfo title="Featured" last>
                        CONTENT
                        </BlockInfo>

                    </div>



               </div>
               
            </div>

                <style jsx>
                {`  
                   .ePoster {
                       display: flex;
                       align-items: center;

                       margin-top: 20px;
                       padding-bottom: 20px;
                       border-bottom: 1px solid white;
                   }
                   .main {
                     padding-left: var(--main-margin);
                    }

                    .sections {
                        display: flex;
                        flex-direction: row;


                    }

                    .section {
                        width: 260px;
                    }
                   
                    .list {
                        display: flex;
                        flex-grow: 2;
                        margin-right: 25px;
                        flex-direction: column;

                    }

                    .blocks {
                    }

                   
                  
                `}

                </style>
        </TplGlobal>

        
    )
}

export default TplList;